@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.action-infos-thumbnail-wrapper {
  position: absolute;
  z-index: 2;
  width: 152px;
  height: 152px;

  .icon_play {
    position: relative;
    .icon-ant-play-circle {
      position: absolute;
    }
  }

  &:hover {
    background-color: rgba(0, 0, 0, 0.3);
  }

  &:hover > .icon_play {
    display: block;
    animation: fadeIn ease-in 0.6s;
  }

  .icon_play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);

    display: none;
    width: 48px;
    height: 48px;
    color: #fff;
    font-size: 48px;
    opacity: 0.8;
  }
}
